/** The mixins below are shared between md-menu and md-select */
/**
 * This mixin adds the correct panel transform styles based
 * on the direction that the menu panel opens.
 */
/**
 * The max-height of the panel, currently matching md-select value.
 * TODO: Check value with MD team.
 */
/** When in "below" position, the panel needs a slight y-offset to ensure the input underline displays. */
/** When in "above" position, the panel needs a larger y-offset to ensure the label has room to display. */
.mat-autocomplete-panel {
  box-shadow: 0px 5px 5px -3px rgba(0, 0, 0, 0.2), 0px 8px 10px 1px rgba(0, 0, 0, 0.14), 0px 3px 14px 2px rgba(0, 0, 0, 0.12);
  min-width: 112px;
  max-width: 280px;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  visibility: hidden;
  max-height: 256px;
  position: relative; }
  .mat-autocomplete-panel.mat-autocomplete-panel-below {
    top: 6px; }
  .mat-autocomplete-panel.mat-autocomplete-panel-above {
    top: -24px; }
  .mat-autocomplete-panel.mat-autocomplete-visible {
    visibility: visible; }
  .mat-autocomplete-panel.mat-autocomplete-hidden {
    visibility: hidden; }
